<template>
  <div>
    <div
      v-show="modal"
      style="
        position: fixed;
        width: 100vw;
        height: 100vh;
        left: 0;
        top: 0;
        z-index: 1000000004;
        background: black;
        opacity: 0.6;
      "
    ></div>
    <div
      v-show="modal"
      style="
        position: fixed;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
        z-index: 1000000005;
        min-width: 50vw;
      "
    >
      <div
        class="rounded-lg shadow p-3 bg-white animate__animated animate__bounceIn"
      >
        <button
          type="button"
          @click="modal = false"
          class="btn btn-sm btn-dark float-right"
        >
          x
        </button>
        <div class="p-3">
          <h4>Form Input</h4>
          <hr />
          <form action="" @submit.prevent="submitForm">
            <div class="sm-form">
              <input
                type="text"
                id="field"
                name="field"
                class="form-control p-2 rounded-lg shadow"
                placeholder="field"
                v-model="vdata['field']"
              />
            </div>
            <hr />
            <button type="submit" class="btn btn-sm btn-success">Simpan</button>
          </form>
        </div>
      </div>
    </div>
    <div
      v-show="menu"
      class=""
      style="position: fixed; z-index: 10000; right: 50px; top: 50%"
    >
      <div class="mt-2">
        <button
          @click="toggleDrawMode"
          class="btn btn-sm"
          :class="drawMode ? 'btn-dark' : 'btn-outline-dark'"
        >
          <i class="fa fa-pencil-square-o" aria-hidden="true"></i>
        </button>
      </div>
      <div class="mt-2">
        <button
          @click="toggleEraseMode"
          class="btn btn-sm"
          :class="eraseMode ? ' btn-danger ' : 'btn-outline-danger'"
        >
          <i class="fa fa-eraser" aria-hidden="true"></i>
        </button>
      </div>
      <div class="mt-2">
        <button @click="saveBox" class="btn btn-sm btn-success">
          <i class="fa fa-floppy-o" aria-hidden="true"></i>
        </button>
      </div>
      <!-- Zoom-in button -->
      <div class="mt-2">
        <button @click="zoomIn" class="btn btn-sm btn-info">
          <i class="fa fa-search-plus" aria-hidden="true"></i>
        </button>
      </div>

      <!-- Zoom-out button -->
      <div class="mt-2">
        <button @click="zoomOut" class="btn btn-sm btn-info">
          <i class="fa fa-search-minus" aria-hidden="true"></i>
        </button>
      </div>
    </div>
    <div id="containerCanvas" style="width: 100%; overflow: scroll">
      <div>
        <canvas id="canvas"></canvas>
      </div>
    </div>
    <div class="row p-4">
      <div
        v-for="(box, index) in listBoxes"
        :key="index"
        class="col-sm-3 text-xs p-1"
      >
        <div class="p-2 shadow rouded-sm">
          <p class="m-0 p-0 font-semibold">Field: {{ box.field }}</p>
          <p class="m-0 p-0">x: {{ box.x }}</p>
          <p class="m-0 p-0">y: {{ box.y }}</p>
          <p class="m-0 p-0">width: {{ box.width }}</p>
          <p class="m-0 p-0">height:{{ box.height }}</p>
          <hr />
          <button
            type="button"
            @click="deleteRectangle(index)"
            class="btn btn-sm btn-danger mr-2 text-xs"
          >
            Delete
          </button>
        </div>
      </div>
    </div>
    <div style="height: 300px"></div>
  </div>
</template>
<script>
import auto from "@/plugins/autophp";
let sdb = new auto();
var canvas;

export default {
  data() {
    return {
      menu: false,
      drawMode: false, // State untuk menggambar
      eraseMode: false, // State untuk menghapus
      listBoxes: [], // Array untuk menyimpan objek persegi
      modal: false,
      imageUrl: "",
      backgroundImg: null,
      move: false,
      vdata: {},
      zoomLevel: 1,
      zoomReverse: 1,
      item: {},
    };
  },
  methods: {
    async submitForm() {
      let that = this;
      const docRef = firebase.doc(
        firebase.firestore,
        "ocr-layouts",
        this.$route.query.id
      );
      this.listBoxes[this.listBoxes.length - 1]["field"] = this.vdata.field;
      let boxes = this.listBoxes.map((e) => {
        return {
          x: e.x,
          y: e.y,
          width: e.width,
          height: e.height,
          field: e.field ?? "",
        };
      });
      firebase
        .updateDoc(docRef, { boxes: boxes })
        .then(() => {
          that.modal = false;
          that.vdata = {};
        })
        .catch((error) => {
          console.error("Error updating document:", error);
        });
    },
    saveBox(index = -1,reload='false') {
      let that = this;
      const docRef = firebase.doc(
        firebase.firestore,
        "ocr-layouts",
        this.$route.query.id
      );
      let boxes = this.listBoxes.map((e) => {
        return {
          x: e.x,
          y: e.y,
          width: e.width,
          height: e.height,
          field: e.field ?? "",
        };
      });
      firebase
        .updateDoc(docRef, { boxes: boxes })
        .then(() => {
          if (index != -1) {
            // that.listBoxes.forEach((square) => {
            //     canvas.remove(square);
            // });
            // canvas._objects.forEach(e=>{
            //   canvas.remove(e)
            // })
            that.listBoxes = [];
            that.getData(reload);
          }
        })
        .catch((error) => {
          console.error("Error updating document:", error);
        });
    },
    zoomIn() {
      this.zoomLevel *= 1.2; // You can adjust the zoom factor as needed
      this.zoomReverse /= 1.2; // You can adjust the zoom factor as needed
      this.updateZoom();
    },
    zoomOut() {
      this.zoomLevel /= 1.2; // You can adjust the zoom factor as needed
      this.zoomReverse *= 1.2; // You can adjust the zoom factor as needed
      this.updateZoom();
    },
    updateZoom() {
      if (this.backgroundImg) {
        canvas.setZoom(this.zoomLevel);
      }
      // Update the positions of the squares based on the new zoom level
      const zoomFactor = this.zoomLevel / canvas.getZoom();
      this.listBoxes.forEach((square) => {
        square.left *= zoomFactor;
        square.top *= zoomFactor;
        square.width *= zoomFactor;
        square.height *= zoomFactor;
      });
      const containerCanvas = document.getElementById("containerCanvas");
      const upperCanvas = document.querySelector(".upper-canvas");
      const lowerCanvas = document.querySelector(".lower-canvas");
      const mainCanvas = document.querySelector(".canvas-container");
      console.dir(upperCanvas);
      const newWidth = this.canvasWidth * this.zoomLevel;
      const newHeight = this.canvasHeight * this.zoomLevel;
      canvas.renderAll();
    },
    toggleDrawMode() {
      this.drawMode = !this.drawMode; // Toggle drawMode on/off
      this.eraseMode = false; // Turn off eraseMode when drawMode is on
    },
    toggleEraseMode() {
      this.eraseMode = !this.eraseMode; // Toggle eraseMode on/off
      this.drawMode = false; // Turn off drawMode when eraseMode is on
    },
    deleteRectangle(index) {
      const box = this.listBoxes[index];
      if (box) {
        canvas.remove(box);
        this.listBoxes.splice(index, 1); // Hapus objek persegi dari array
      }
      this.saveBox();
    },
    async getData(render = "true") {
      let that = this;
      const docRef = firebase.doc(
        firebase.firestore,
        "ocr-layouts",
        that.$route.query.id
      );
      firebase
        .getDoc(docRef)
        .then((docSnapshot) => {
          if (docSnapshot.exists()) {
            let obj = docSnapshot.data();
            that.item = obj;
            if (obj.boxes) {
              that.listBoxes = obj.boxes;
            }
            that.imageUrl = `https://hit-ocr.online/uploaded/` + obj.filename;
            if (render == "true") {
              that.renderCanvas(that.imageUrl);
            } else if (render == "false") {
              let count = that.listBoxes.length;
              that.listBoxes.forEach((e) => {
                that.createSquare(e.x, e.y, e.width, e.height, e.field);
              });
              that.listBoxes.splice(0, count);
            }else if(render=='reload'){
              if(that.zoomLevel!=1){
                location.reload()
              }else{
                let count = that.listBoxes.length;
                that.listBoxes.forEach((e) => {
                  that.createSquare(e.x, e.y, e.width, e.height, e.field);
                });
                that.listBoxes.splice(0, count);
              }
            }
          } else {
            console.log("Document does not exist");
          }
        })
        .catch((error) => {
          console.error("Error getting document:", error);
        });
    },
    createSquare(x, y, width, height, field) {
      let that = this;
      x *= this.zoomReverse;
      y *= this.zoomReverse;
      width *= this.zoomReverse;
      height *= this.zoomReverse;
      const square = new fabric.Rect({
        left: x,
        top: y,
        width: width,
        height: height,
        fill: "transparent",
        stroke: "red",
        strokeWidth: 2,
      });
      canvas.add(square);
      canvas.renderAll();
      // Tambahkan objek persegi ke dalam array dengan properti x, y, width, dan height
      square["x"] = x;
      square["y"] = y;
      square["field"] = field;
      that.listBoxes.push(square);
    },
    renderCanvas(imageUrl) {
      let that = this;
      var containerCanvas = document.getElementById("containerCanvas");
      canvas = new fabric.Canvas("canvas");
      var imageURL = imageUrl;
      let count = that.listBoxes.length;
      setTimeout(() => {
        that.listBoxes.forEach((e) => {
          that.createSquare(e.x, e.y, e.width, e.height, e.field);
        });
        that.listBoxes.splice(0, count);
        that.menu = true;
      }, 2000);
      // Mengaktifkan mode menggambar saat mouse down
      canvas.on("mouse:down", (event) => {
        const { x, y } = event.pointer;
        this.startX = x;
        this.startY = y;
        if (this.eraseMode) {
          const target = event.target;
          if (target && target.type === "rect") {
            this.listBoxes = this.listBoxes.filter((box) => box !== target); // Hapus objek persegi dari array
            canvas.remove(target);
            that.saveBox(0);
          }
        }
      });
      var objs = canvas.getObjects();
      var group = new fabric.Group(objs);
      canvas.add(group);
      canvas.on("object:moved", (event) => {
        that.move = true;
        const target = event.target;
        if (event.target._objects) {
          let objects = event.target._objects;
          let group = objects[0].group;
          objects.forEach((obj, index) => {
            let objectLeft = obj.left;
            let objectTop = obj.top;
            if (group == undefined) {
            } else {
              let groupLeft = group.left;
              let groupTop = group.top;
              let objectInGroupLeft = objectLeft + groupLeft + group.width / 2;
              let objectInGroupTop = objectTop + groupTop + group.height / 2;
              that.listBoxes[index].x = objectInGroupLeft;
              that.listBoxes[index].y = objectInGroupTop;
              console.log(objectInGroupLeft, objectInGroupTop);
            }
          });
          that.saveBox(-1);
        } else {
          if (target && target.type === "rect") {
            const index = this.listBoxes.indexOf(target);
            const box = this.listBoxes[index];
            if (index !== -1) {
              this.listBoxes[index].x = target.left;
              this.listBoxes[index].y = target.top;
              this.saveBox(-1);
            }
          }
        }
      });

      canvas.on("object:modified", (event) => {
        that.move = true;
        let target = event.target;
        console.log(event)
        if (event.action != "drag") {
          if (target && target._objects) {
            let objects = target._objects;
            let group = objects[0].group;

            if (group) {
              let groupScaleX = group.scaleX;
              let groupTop = group.top;
              let groupLeft = group.left;
              let groupScaleY = group.scaleY;
              let groupCenterX = group.left + group.width / 2;
              let groupCenterY = group.top + group.height / 2;
              console.log(groupTop,groupCenterY,groupScaleY)
              objects.forEach((obj, index) => {
                // Modify the scale of each object relative to the group's scale
                this.listBoxes[index].scaleX *= groupScaleX;
                this.listBoxes[index].scaleY *= groupScaleY;
                this.listBoxes[index].width *= groupScaleX;
                this.listBoxes[index].height *= groupScaleY;
                // HOW TO SET LEFT AND TOP
                 let objectLeft = (obj.left - groupLeft) * groupScaleX + groupLeft;
                let objectTop = (obj.top - groupTop) * groupScaleY + groupTop;
                this.listBoxes[index].left = objectLeft;
                // this.listBoxes[index].top = groupCenterY+(obj.top*groupScaleY);
                 // Set the left and top properties for each object
                canvas.remove(this.listBoxes[index]);
              });
              that.saveBox(1,'reload');
            }
          } else {
            const target = event.target;
            if (target && target.type === "rect") {
              const index = this.listBoxes.indexOf(target);
              const box = this.listBoxes[index];
              if (index !== -1) {
                if (this.listBoxes[index].scaleY != 1) {
                  console.log("scaleY");
                  this.listBoxes[index].height =
                    this.listBoxes[index].height * target.scaleY;
                }
                if (this.listBoxes[index].scaleX != 1) {
                  console.log("scaleX");
                  this.listBoxes[index].width =
                    this.listBoxes[index].width * target.scaleX;
                }
                that.listBoxes.forEach((e) => {
                  canvas.remove(e);
                });
                this.saveBox(1);
              }
            }
          }
        }
      });

      // Menggambar persegi saat mouse up dan mode menggambar aktif
      canvas.on("mouse:up", (event) => {
        if (this.drawMode) {
          const { x, y } = event.pointer;
          const width = x - this.startX;
          const height = y - this.startY;
          let obj = { x, y, width, height };
          if (height != 0) {
            that.createSquare(this.startX, this.startY, width, height, "");
            that.modal = true;
          }
        } else {
          if (!that.move) {
            const { x, y } = event.pointer;
            const width = x - this.startX;
            const height = y - this.startY;
            containerCanvas.scrollLeft =
              containerCanvas.scrollLeft + width * -1;
            containerCanvas.scrollTop = containerCanvas.scrollTop + height * -1;
          } else {
            that.move = false;
          }
        }
      });

      // Memuat gambar latar belakang
      // Load the background image
      fabric.Image.fromURL(imageURL, (img) => {
        img.set({ selectable: false });
        // Store the background image reference in the variable
        that.backgroundImg = img;
        canvas.setBackgroundImage(img, () => {
          const imageWidth = img.width;
          const imageHeight = img.height;
          canvas.setWidth(imageWidth);
          canvas.setHeight(imageHeight);
          that.canvasWidth = imageWidth;
          that.canvasHeight = imageHeight;
          containerCanvas.style.height = imageHeight;
          containerCanvas.style.width = imageWidth;
          canvas.renderAll();
        });
      });
    },
  },
  mounted() {
    this.getData();
  },
};
</script>
